<script>
    import { 
        Col, 
        Container, 
        Row,
        Button,
        Card,
        CardBody,
        CardFooter,
        CardHeader,
        CardSubtitle,
        CardText,
        CardTitle,
        Icon,
    } from 'sveltestrap';
    import { onMount, afterUpdate } from 'svelte';
	import { location, push, querystring } from 'svelte-spa-router';
    import { fly, fade, slide } from 'svelte/transition';
    
    export let params;
    export let baseUrl;
    export let disqusShortname;

    // 初始fetch
    let post = {};
    onMount(async () => {
        let data = await fetch(baseUrl + '/' + params.path + '/index.json').then(data => data.json());
        post = await data.result;
    });

    //in的時候延遲
    function fade_d(node, {
	    delay = 500,
	    duration = 200
    }) {
	    const o = +getComputedStyle(node).opacity;
	    return {
		    delay,
		    duration,
		    css: t => `opacity: ${t * o}`
	    };
    }





    //
    function addCodeBlockTitle () {
        let contentList = document.getElementsByClassName('post-content');
        if (contentList.length === 0) return;

        for (let i = 0; i < contentList.length; i++) {
            //create new parent, all code block will be 'div.highlight div pre' or 'div.highlight div table'
            let codeList = contentList[i].getElementsByTagName('code');
            for (let j = 0; j < codeList.length; j++) {
                let codeParent = codeList[j].closest('pre');
                if (!codeParent) continue;
                if (codeParent.closest('td')) continue;
                let div = document.createElement('div');
                if (!codeParent.closest('div.highlight')) {
                    div.classList.add('highlight');
                    div.appendChild(document.createElement('div'));
                    div.firstChild.appendChild(codeParent.cloneNode(true));
                }else {
                    div.appendChild(codeParent.cloneNode(true));
                }
                codeParent.replaceWith(div);   
            }
            //append code-name in 'div.highlight' 
            let hlList = document.body.getElementsByClassName('highlight');
            for(let j = 0; j < hlList.length; j++){
                let hl = hlList[j];
                let hlInner = hl.firstElementChild.firstElementChild;
                let code = (hlInner.nodeName == 'TABLE') ? hlInner.firstElementChild.firstElementChild.childNodes[2].firstElementChild.firstElementChild : hlInner.firstElementChild;
                let codeNameText =  code ? code.className.split(':')[1] : null;
                if (!codeNameText) continue;
                let codeNameSpan = document.createElement('span');
                codeNameSpan.textContent = codeNameText;
                codeNameSpan.classList.add('code-name');
                hl.insertBefore(codeNameSpan, hlInner.parentNode);
            }
        }
    }


    function isLoadedScript(lib) {
        return document.querySelectorAll('[src="' + lib + '"]').length > 0;
    }


    function addDisqus() {
        console.log(disqusShortname)
        window.disqus_config = function () {
            this.page.url = baseUrl + '/' + params.path;
        }
        if (window.DISQUS) {
            window.DISQUS.reset({
                reload: true,
            });
            return;
        }
        
        let disqusSrc = `https://${disqusShortname}.disqus.com/embed.js`
        if (!isLoadedScript(disqusSrc)) {
            let d = document, s = d.createElement('script');
            s.src = disqusSrc;
            s.setAttribute('data-timestamp', +new Date());
            (d.head || d.body).appendChild(s);
        }
    }


    afterUpdate(() => {
        addCodeBlockTitle();
        if (!Object.keys(post).length > 0) return;
        addDisqus();
    });


    
</script>


<Container class="mt-3">
    <Row>
        <Col xs="12" sm="12" md="12" lg="12">
            {#if Object.keys(post).length > 0 }
            <div class="post-card mb-2"  in:fade_d out:fade>
                <Card>
                    <CardHeader>
                        <CardSubtitle class="h6 card-subtitle text-muted">
                            Posted on
                            {['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'][parseInt(post.params.date.split('-')[1] - 1)]} 
                            {parseInt(post.params.date.split('-')[2].split('T')[0])},
                            {post.params.date.split('-')[0]}
                        </CardSubtitle>
                        <CardTitle class="h5">{post.params.title}</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <div class="post-content">
                            {@html post.content}
                        </div>
                    </CardBody>
                    <CardFooter>
                        {#each post.params.categories as category}
                            <button class="btn btn-warning btn-custom mr-1">
                                <Icon name="folder-fill" />
                                {category}
                            </button>
                        {/each}
                        {#each post.params.tags as tag}
                            <button class="btn btn-success btn-custom mr-1" on:click={() => push('/?&tag=' + tag)}>
                                <Icon name="tag-fill" />
                                {tag}
                            </button>
                        {/each}
                    </CardFooter>
                </Card>
            </div>

            <div class="post-card mb-2"  in:fade_d out:fade>
                <Card>
                    <CardBody>
                        <div id="disqus_thread"></div>
                    </CardBody>
                    
                </Card>
            </div>
            {/if}
        </Col>
    </Row>
</Container>




<style>
    .post-card :global(.card .card-header .card-title) {
        margin-bottom: 0px;
    }

    .post-card :global(.card .card-header .card-subtitle) {
        margin-top: -0.2rem;
    }

    .post-content > :global(.highlight) {
        background-color: #272822 !important;
        color: #f8f8f2;
        padding: 4px;
        border-radius: 4px;
    }

    .post-content > :global(.highlight > .code-name) {
        /*margin-bottom: 10px;*/
        margin: 4px;
        display: inline-block;
        position: relative;
        padding: 4px 8px;
        background-color: #E7E9EB;
        color: #485A60;
        font-size: 13px;
        font-weight: 400;
        border-radius: 4px;
    }

    .post-content > :global(.highlight > div > pre) {
        margin: 6px;
    }


    .btn-custom {
        padding: 0.35em 0.65em;
        font-size: 0.75em;
        font-weight: 700;
        line-height: 1;
        border-radius: 0.5rem;
    }

    .mr-1 {
        margin-right: 2px;
    } 
</style>