<script>
	import Router from 'svelte-spa-router';
	import {wrap} from 'svelte-spa-router/wrap';

	import {
    	Collapse,
    	Navbar,
    	NavbarToggler,
    	NavbarBrand,
    	Nav,
    	NavItem,
    	NavLink,
    	Dropdown,
    	DropdownToggle,
    	DropdownMenu,
    	DropdownItem
  	} from 'sveltestrap';

	import NotFound from './routes/NotFound.svelte';
	import PostList from './routes/PostList.svelte';
	import Post from './routes/Post.svelte';
	import { location, push, querystring } from 'svelte-spa-router';

	export let siteTitle;
	export let baseUrl;
	export let disqusShortname;

  	let isOpen = false;

  	function handleUpdate(event) {
    	isOpen = event.detail.isOpen;
  	}



	const routes = {
		'/': wrap({
			component: PostList,
			props: {
				baseUrl: baseUrl
			}
		}),
		'/blog/:path': wrap({
			component: Post,
			props: {
				baseUrl: baseUrl,
				disqusShortname: disqusShortname
			}
		}),
		'*': NotFound
	}



</script>
  

  
<svelte:head>
	<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootswatch@5.0.2/dist/quartz/bootstrap.css">
	<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap-icons@1.4.1/font/bootstrap-icons.css">

	<title>{siteTitle}</title>
</svelte:head>


<Navbar color="dark" dark expand="md">
	<NavbarBrand href="/#/">{siteTitle}</NavbarBrand>
	<NavbarToggler on:click={() => (isOpen = !isOpen)} />
	<Collapse {isOpen} navbar expand="md" on:update={handleUpdate}>
	  	<Nav class="ml-auto" navbar>
			<NavItem>
		  		<NavLink href="#/">Posts</NavLink>
			</NavItem>
			<NavItem>
			  	<NavLink disabled>Works</NavLink>
			</NavItem>
			<NavItem>
				<NavLink disabled>About</NavLink>
		  	</NavItem>
			<!--
			<Dropdown nav inNavbar>
			  	<DropdownToggle nav caret>Options</DropdownToggle>
			  	<DropdownMenu right>
					<DropdownItem>Option 1</DropdownItem>
					<DropdownItem>Option 2</DropdownItem>
					<DropdownItem divider />
					<DropdownItem>Reset</DropdownItem>
			  	</DropdownMenu>
			</Dropdown>
			-->
	  	</Nav>
	</Collapse>
</Navbar>


<main>
	<Router {routes}/>
</main>


