<script>
    import { 
        Col, 
        Container, 
        Row,
        Button,
        Card,
        CardBody,
        CardFooter,
        CardHeader,
        CardSubtitle,
        CardText,
        CardTitle,
        Icon,
        Fade,
    } from 'sveltestrap';
    import { paginate, PaginationNav } from 'svelte-paginate';
    import { onMount } from 'svelte';
	import { location, push, querystring, replace } from 'svelte-spa-router';
    import { fly, fade, slide } from 'svelte/transition';

    let currentPage = 1; // 當前頁面
  	let pageSize = 10; // 頁長
    let posts = []; // 初始fetch的貼文
	let filteredPosts = []; // 過濾後的貼文
    let displayedPosts = [];
    let selectedTags = []; // 選取的標籤(過濾用)
	let selectedCategory = ''; // 選取的分類(過濾用)
    let currentKeyword = ''; // 關鍵字(過濾用)
    let keyword = ''; // binding關鍵字用的input
    let postsDisplayed = true;

    export let baseUrl;

    // 初始fetch
    onMount(async () => {
	      let data = await fetch(baseUrl + '/posts/index.json').then(data => data.json());
	      posts = data.result.pages;
          setFilterConditions($querystring);
          selections = getSideButtonRefs(posts);
    });


	// svelte-paginate換頁用
  	$: paginatedPosts = paginate({items: filteredPosts, pageSize, currentPage});
    
    // 監控querystring
    $: setFilterConditions($querystring);

    // 監控過濾條件
    $: setFilteredPosts(posts, selectedCategory, selectedTags, currentKeyword);

    // 監控當頁貼文(迴圈顯示特效用)
    $: setDisplayedPosts(paginatedPosts);

    
          


    async function setDisplayedPosts(paginatedPosts) {
        postsDisplayed = false;
        scrollTo({top: 0, left: 0, behavior: 'smooth'});
        let l = displayedPosts.length;
        /*while (displayedPosts.length > 0) {
            displayedPosts = [...displayedPosts.slice(0, displayedPosts.length - 1)]
            await sleep(parseInt(200 / l, 10));
        }
        for (let post of paginatedPosts) {
            await sleep(100);
            displayedPosts = [...displayedPosts, post];
        }*/
        displayedPosts = [];
        await sleep(250);
        displayedPosts = paginatedPosts;
        postsDisplayed = true;
    }

    /*雜七雜八*/
    // 檢查array
    function checkArray(target, item) {
        if (item && target) {
            return item.every(v => target.includes(v));
        }
        return false;
    }


	// 一個阻塞機制
	function sleep(ms) {
		return new Promise( resolve => setTimeout(resolve, ms));
	}


 

    /* 過濾貼文用 */

    // 測試用標籤
    function updateQuery(newQuery, name) {
        let oldQuery = getOtherQuery(name);
        if (newQuery.length) newQuery.concat(oldQuery);
        if (postsDisplayed) push((newQuery.length) ? '#/?' + newQuery.join('&') : '/');
    }

    function getOtherQuery(name) {
        let arr = [];
        for (const q of $querystring.split('&')) {
            if (q.includes(`${name}=`) || q.length < 1) continue;
            arr.push(q);
        }
        return arr;
    }


    function updateMultipleQuery(targetArr, name, value) {
        let newArr = targetArr.includes(value) ? targetArr.filter(e => e !== value) : [...targetArr, value];
        let newQuery = newArr.map(el => `${name}=${escape(el)}`);
        updateQuery(newQuery, name);
    }

    function updateSingleQuery(target, name, value) {
        let newQuery = (value !== target && value) ? [`${name}=${escape(value)}`] : [];
        updateQuery(newQuery, name);
    }


    function setTagQuery(tag) {
        updateMultipleQuery(selectedTags, 'tag', tag);
    }

    function setCategoryQuery(category) {
        updateSingleQuery(selectedCategory, 'category', category);
    }


    function setKeywordQuery(keyword) {
        updateSingleQuery(currentKeyword, 'keyword', keyword);
    }



    // 將query塞進過濾條件
    function setFilterConditions(querystring) {
        let category = '';
        let tags = [];
        let keyword = '';
        for (const q of querystring.split('&')) {
			if (q.includes('category=')) {
				category = unescape(q.slice(9));
			} else if (q.includes('tag=')) {
				tags.push(q.slice(4).replace('%20', ' '));
			} else if (q.includes('keyword=')) {
                keyword = unescape(q.slice(8));
            }
		}
        selectedCategory = category;
        selectedTags = tags;
        currentKeyword = keyword;
    }


    // 塞入已過濾貼文
    function checkConditions(origPosts, selectedCategory, selectedTags, currentKeyword) {
        console.log(selectedCategory)
        let result = [];
        for (const p of origPosts) {
			if (checkArray(p.params.tags, selectedTags) && (p.params.categories.includes(selectedCategory) || !selectedCategory)) result.push(p);
		}
        return checkKeyword(result, currentKeyword);
    }


    // 取得所有元素or關鍵字的index
    function getIndices(el, target) {
        var indices = [];
        let idx = target.indexOf(el);
        while (idx !== -1) {
            indices.push(idx);
            idx = target.indexOf(el, idx + 1);
        }
        return indices;
    }


    // 關鍵字高亮
    function highlightKeyword(origStr, keyword) {
        let l_keyword = keyword.toLowerCase();
        let l_str = origStr.toLowerCase();
        let keywordIdx = l_str.indexOf(l_keyword);
        if (keywordIdx === -1) return origStr;
        let indices = getIndices(l_keyword, l_str);
        let origParts = [];
        for (const idx of indices) {
            origParts.push(origStr.substring(keywordIdx, keywordIdx + keyword.length))
        }
        let prefix = '<span style="background-color:#00a381">';
        let suffix = '</span>';
        let newStr = origStr;
        for (const part of origParts) {
            let re = new RegExp(`(?!${prefix})${part}(?!${suffix})`, 'g');
            newStr = newStr.replace(re, `${prefix}${part}${suffix}`);
        }
        return newStr;
        
    }

    // 檢查關鍵字
    function checkKeyword(origPosts, keyword) {
        if (!keyword) return origPosts;
        let l_keyword = keyword.toLowerCase();
        let result = [];
        for (const p of origPosts) {
            if (!(p.params.title.toLowerCase().includes(l_keyword) || p.summary.toLowerCase().includes(l_keyword))) continue;
            p.params.title = highlightKeyword(p.params.title, keyword);
            p.summary = highlightKeyword(p.summary, keyword);
            result.push(p);
        }
        return result;
    }

    // 過濾後的貼文直接塞global var
    function setFilteredPosts(posts, selectedCategory, selectedTags, currentKeyword) {
        let checkPosts = new Promise(resolve => {
            let posts_clone = JSON.parse(JSON.stringify(posts));
            let filteredResults = checkConditions(posts_clone, selectedCategory, selectedTags, currentKeyword);
            resolve(filteredResults);
        });
        checkPosts.then(data => {
            filteredPosts = data;
            currentPage = 1;
        });
    }


    // 右側試作
    let selections = {
        tags:{}, 
        categories: {}
    };

    function calcSize() {
        return 1
    }

    function collectPostsParams(posts) {
        let collection = {
            tags:{}, 
            categories: {}
        };

        for (const post of posts) {
            for (const key of Object.keys(collection)) {
                for (const name of post.params[key]) {
                    (collection[key][name]) ? collection[key][name] += 1 : collection[key][name] = 1;
                }
            }
        }
        return collection;
    }

    function getSideButtonRefs(posts) {
        let collection = collectPostsParams(posts);
        let sizeRefs = {};
        for (const key of Object.keys(collection)) {
            sizeRefs[key] = {};
            let counts = [...new Set(Object.values(collection[key]))].sort((a, b) => b - a);
            for (const name of Object.keys(collection[key])) {
                sizeRefs[key][name] = 1 - counts.indexOf(collection[key][name]) / counts.length / 2;
            }
        }
        return sizeRefs;
    }

    function scrollToTop() {
        scrollTo({top: 0, left: 0, behavior: 'smooth'});
    }

</script>


<Container class="mt-3">
    <Row>
        <Col xs="12" sm="12" md="12" lg="9">
            {#each displayedPosts as post, i}
                    <div class="post-card mb-2" in:slide|local out:fly="{{ y: -20, duration: 200 }}">
                        <Card>
                            <CardHeader>
                                <CardSubtitle class="h6 card-subtitle text-muted">
                                    Posted on
                                    {['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'][parseInt(post.params.date.split('-')[1] - 1)]} 
                                    {parseInt(post.params.date.split('-')[2].split('T')[0])},
                                    {post.params.date.split('-')[0]}
                                </CardSubtitle>
                                <CardTitle class="h5">{@html post.params.title}</CardTitle>
                            </CardHeader>
                            <CardBody>

                            
                                <CardText>
                                    {@html post.summary}
                                </CardText>
                                <Button color="primary" size="sm" on:click={() => {push('/blog/' + post.path.replaceAll('/', '%2F')); scrollToTop();}}>Continue Reading</Button>
                            </CardBody>
                            <CardFooter>
                                {#each post.params.categories as category}
                                    {#if selectedCategory === category}
                                        <button class="btn btn-warning btn-custom active mr-1" on:click={() => setCategoryQuery(category)}>
                                            <Icon name="check2" />
                                            {category}
                                        </button>

                                    {:else}
                                        <button class="btn btn-warning btn-custom mr-1" on:click={() => setCategoryQuery(category)}>
                                            <Icon name="folder-fill" />
                                            {category}
                                        </button>
                                    {/if}

                                {/each}
                                {#each post.params.tags as tag}
                                    {#if selectedTags.includes(tag)}
                                        <button class="btn btn-success btn-custom active mr-1" on:click={() => setTagQuery(tag)}>
                                            <Icon name="check2" />
                                            {tag}
                                        </button>
                                    {:else}
                                        <button class="btn btn-success btn-custom mr-1" on:click={() => setTagQuery(tag)}>
                                            <Icon name="tag-fill" />
                                            {tag}
                                        </button>
                                    {/if}
                                {/each}
                            </CardFooter>
                        </Card>
                    </div>
        
		    {/each}


            {#if paginatedPosts.length === displayedPosts.length && paginatedPosts.length}
                <div class="dark-pagination-nav" in:fade out:fly="{{ y: 100, duration: 100 }}">
                    <PaginationNav
                        totalItems="{filteredPosts.length}"
                        pageSize="{pageSize}"
                        currentPage="{currentPage}"
                        limit="{1}"
                        showStepOptions="{true}"
                        on:setPage="{(e) => currentPage = e.detail.page}"
                    />
                </div>
            {/if}
        </Col>


        <Col xs="12" sm="12" md="12" lg="3">
            <div in:fly="{{ y: 100, duration: 600 }}" out:fly="{{ y: 100, duration: 200 }}">
                <Card class="mb-3">
                    <CardHeader>
                        <CardTitle style="margin-bottom: 0px;">Search</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <div class="input-group mb-2">
                            <input bind:value={keyword} type="text" class="form-control" placeholder="Enter keyword here...">
                        </div>
                        {#if currentKeyword === keyword && currentKeyword}
                            <button type="submit" class="btn btn-danger" on:click={() => {setKeywordQuery(keyword); keyword = ''}}>Clear</button>
                        {:else}
                            <button class="btn btn-danger" on:click={() => setKeywordQuery(keyword)}>Submit</button>
                        {/if}
                    </CardBody>
                </Card>
            </div>

            <div in:fly="{{ y: 100, duration: 700 }}" out:fly="{{ y: 100, duration: 200 }}">
                <Card class="mb-3">
                    <CardHeader>
                        <CardTitle style="margin-bottom: 0px;">Categories</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <CardText style="font-size: 1.25rem;">
                            {#each Object.entries(selections['categories']) as [category, size]}
                            {#if selectedCategory === category}
                                <button class="btn btn-warning btn-custom active mr-1" style="font-size: {size}rem;" on:click={() => setCategoryQuery(category)}>
                                    <Icon name="check2" />
                                    {category}
                                </button>

                            {:else}
                                <button class="btn btn-warning btn-custom mr-1" style="font-size: {size}rem;" on:click={() => setCategoryQuery(category)}>
                                    <Icon name="folder-fill" />
                                    {category}
                                </button>
                            {/if}
                            {/each}
                        </CardText>
                    </CardBody>
                </Card>
            </div>

            <div in:fly="{{ y: 100, duration: 800 }}" out:fly="{{ y: 100, duration: 200 }}">
                <Card class="mb-3">
                    <CardHeader>
                        <CardTitle style="margin-bottom: 0px;">Tags</CardTitle>
                    </CardHeader>
                    <CardBody>
                        <CardText style="font-size: 1.25rem;">
                            {#each Object.entries(selections['tags']) as [tag, size]}
                            {#if selectedTags.includes(tag)}
                                <button class="btn btn-success btn-custom active mr-1" style="font-size: {size}rem;" on:click={() => setTagQuery(tag)}>
                                    <Icon name="check2" />
                                    {tag}
                                </button>
                            {:else}
                                <button class="btn btn-success btn-custom mr-1" style="font-size: {size}rem;" on:click={() => setTagQuery(tag)}>
                                    <Icon name="tag-fill" />
                                    {tag}
                                </button>
                            {/if}
                            {/each}
                        </CardText>
                    </CardBody> 
                </Card>

                </div>
            </Col>


    </Row>
</Container>



<style>
    .post-card :global(.card .card-header .card-title) {
        margin-bottom: 0px;
    }

    .post-card :global(.card .card-header .card-subtitle) {
        margin-top: -0.2rem;
    }

    .dark-pagination-nav :global(.pagination-nav) {
        display: flex;
        justify-content: center;
        /*background: hsl(200, 80%, 5%);*/
        border-radius: 3px;
        /*box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);*/
    }
    .dark-pagination-nav :global(.option) {
        padding: 10px;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: 0.2s all ease-out;
        user-select: none;
        color: hsl(200, 90%, 90%);
    }
    .dark-pagination-nav :global(.option svg path) {
        fill: hsl(200, 90%, 90%);
    }
    .dark-pagination-nav :global(.option:first-child) {
        border-radius: 3px 0 0 3px;
    }
    .dark-pagination-nav :global(.option:last-child) {
        border-radius: 0 3px 3px 0;
    }
    .dark-pagination-nav :global(.option.number),
    .dark-pagination-nav :global(.option.ellipsis) {
        padding: 10px 15px;
    }
    .dark-pagination-nav :global(.option:hover) {
        background: #7da0d1;
        cursor: pointer;
    }
    .dark-pagination-nav :global(.option.active) {
        color: var(--primary);
    }



    .btn-custom {
        padding: 0.35em 0.65em;
        font-size: 0.75em;
        font-weight: 700;
        line-height: 1;
        border-radius: 0.5rem;
    }

    .mr-1 {
        margin-right: 2px;
    }
</style>